import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { BlogSiteServiceService } from '../../services/blog-site-service.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  UserLogin: FormGroup;
  submitted = false;
  invalid: string;
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private blogSiteServiceService: BlogSiteServiceService
  ) { }

  ngOnInit(): void {
    this.UserLogin = this.fb.group({
      userName: [
        '',
        [
          Validators.required,
        ],
      ],
      password: ['', [Validators.required, Validators.minLength(6)]],
    });
  }

  OnSubmit() {
    this.submitted = true;
    if (this.UserLogin.invalid) {
      console.log('invalid');
      return;
    }
    let authorization: string;
    this.blogSiteServiceService.checkUserCredentials(this.UserLogin.value).subscribe(
      (data) => {
        this.blogSiteServiceService.getToken().subscribe(
          (token) => {
            authorization = token.jwttoken;
            this.blogSiteServiceService.storeUserData(
              data.userName,
              authorization
            );
            this.router.navigateByUrl('/home');
          },
          (err) => {
            alert(err.message);
          }
        );
      },
      (error) => {
        if (error.message.includes('400')) {
          this.invalid = 'Invalid Credentials';
        } else {
          alert(error.message);
        }
      }
    );
  }

}
